@import '../../styles/variables.scss';

.wrapper {
    font-family: $light;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container {
    min-width: 377px;
    padding-bottom: 80px;
    @media screen and (max-width: 400px) {
        min-width: 80%;
    }
}
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 16px;
    &_title {
        margin: 0;
        font-size: 24px;
        color: $color-dark-blue;
        line-height: 28px;
        align-self: center;
    }
    &_logo {
        margin-right: 11px;
        height: 44.5px;
        width: 44.5px;
        align-self: center;
    }
}
.form {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    padding: 20px 18px;
    box-sizing: border-box;
    border-radius: 9px;
    background: #ffffff;
    box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11), 0px 2px 4px rgba(90, 97, 105, 0.12),
        0px 5px 5px rgba(90, 97, 105, 0.06), 0px 3.5px 35px rgba(90, 97, 105, 0.1);
    &_header {
        font-size: 17.5px;
        line-height: 20px;
        align-self: center;
        text-align: center;
        margin: 0 auto 35px;
        color: $color-dark-blue;
    }
    &_inputs {
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
    }
    &_buttons {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
}

.enterButton {
    border: none;
    border-radius: 4px;
    color: #ffffff;
    background: $color-blue;
    min-width: 110px;
    font-size: 11px;
    line-height: 13px;
    padding: 10px 7px;
    cursor: pointer;
}
.registerButton {
    @extend .enterButton;
    color: $color-blue;
    background: white;
}
